h1, .uk-h1 {
  font-size: 32.3px;
  line-height: 1.2;
}

#root > main.uk-container {
  min-height: 80vh;
}

.uk-navbar {
  height: 70px;
}

.avatar-big {
  height: 120px;
  width: 120px;
  text-align: center;
  position: relative;
}

.avatar-preview {
  height: 40px;
  width: 40px;
  text-align: center;
  position: relative;
}

.avatar-preview p {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.chip-label {
  margin-left: 8px;
}

.date-icon {
  padding: 5px;
  background: #f8f8f8;
  border-radius: 500px;
}

.overlap-graph {
  margin-top: 8px;
  margin-bottom: 8px;
}

.overlap-bar {
  background: #e6b7b7;
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 500px;
}

.friend-chip {
  margin-right: 5px;
	position: relative;
}

.friend-chip .tooltip {
  background: black;
  bottom: 100%;
  color: #fff;
  display: block;
  left: -25px;
  margin-bottom: 15px;
  opacity: 0;
  padding: 4px;
  pointer-events: none;
  position: absolute;
	transform: translateY(10px);
	transition: all .25s ease-out;

	font-size: 12px;
  width: 120px;
  text-align: center;
}

/* This bridges the gap so you can mouse into the tooltip without it disappearing */
.friend-chip .tooltip:before {
  bottom: -20px;
  content: " ";
  display: block;
  height: 20px;
  left: 0;
  position: absolute;
  width: 100%;
}  

/* CSS Triangles */
.friend-chip .tooltip:after {
  border-left: solid transparent 10px;
  border-right: solid transparent 10px;
  border-top: solid black 10px;
  bottom: -10px;
  content: " ";
  height: 0;
  left: 50%;
  margin-left: -13px;
  position: absolute;
  width: 0;
}
  
.friend-chip:hover .tooltip {
  opacity: 1;
  pointer-events: auto;
	transform: translateY(0px);
}

.centered-hero {
  margin-top: 40px;
  text-align: center;
}

.user-icons {
  margin: auto;
  width: 70px;
}

.leaflet-div-icon {
  background: none !important;
  border: none !important;
}

.date-range > div {
  width: 36px;
  height: 36px;
  font-size: 12px;
  text-align: center;
  background: #e24c4c;
  color: black;
  margin: 2px;
  border-radius: 8px;
}

.icon-sm {
  transform: scale(0.75);
}

.fullscreen-mobile-menu {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0px;
  left: 0px;
  background: black;
  color: white;
  z-index: 99999;
}

.fullscreen-mobile-menu h4,
.fullscreen-mobile-menu .icono-cross {
  color: white;
}


@media (max-width: 700px) {
  .new-trip-form {
    flex-direction: column;
  }

  .new-trip-form > * {
    margin-top: 20px !important;
  }
}

.flatpickr-input {
  text-align: center;
  margin-left: 1em !important;
  margin-right: 1em !important;
  max-width: 250px !important;
}

.country-picker {
  margin-top: 0 !important;
  min-width: 250px;
}
